<template>
    <div class="videopage">
      <video v-if="Goodsdetails?.videoInfo?.url"
        :src="Goodsdetails?.videoInfo?.url"
        @durationchange="duration = $event.target.duration"
        @timeupdate="currentTime = $event.target.currentTime"
        autoplay
        controls
      ></video>
      <span class="fa fa-times" @click="$router.back()"></span>
      <div class="list">
        <div
        v-if="Goodsdetails?.actorInfo?.avatar"
          class="photo"
          :style="{
            backgroundImage: 'url(' + `${Goodsdetails?.actorInfo?.avatar}` + ')',
          }"
        >
          <div class="attention" @click="attention = !attention">
            <p v-if="attention">关注</p>
            <div class="attentioned" v-else>
              <i class="fa fa-check-circle"></i>
            </div>
          </div>
        </div>
        <div class="star"></div>
        <div class="newicon"></div>
        <div class="shoppingcar" @click="$router.push('/shopcart')"></div>
      </div>
      <div class="infor">
        <h4>@{{ Goodsdetails?.actorInfo?.name }}</h4>
        <p>
          <span>{{ Goodsdetails?.actorInfo?.height }}cm </span>
          <span> {{ Goodsdetails?.actorInfo?.weight }}kg</span> |
          <span>{{ Goodsdetails?.actorInfo?.fansCount }}</span>
        </p>
      </div>
      <div
        class="minigoods"
        @click="
          $router.push({
            path: '/commodity',
            query: { id: Goodsdetails?.itemInfo.itemId },
          })
        "
      >
        <div
        v-if="Goodsdetails?.itemInfo?.cover"
          class="img"
          :style="{
            backgroundImage:
              'url(' +
              `${Goodsdetails?.itemInfo?.cover}` +
              ')',
          }"
        ></div>
  
        <div class="price">
          <h3>{{ Goodsdetails?.itemInfo?.title }}</h3>
          <div class="icon">
            <p v-if="Goodsdetails.subTitle">待秒杀</p>
            <span v-if="Goodsdetails?.itemInfo?.discountPrice">￥{{ parseInt(Goodsdetails?.itemInfo?.discountPrice) }}</span>
          </div>
        </div>
        <div class="buy">立即购买</div>
      </div>
      
    </div>
  </template>
  
  <script>
  import { mapState } from "vuex";
  export default {
    data: function () {
      return {
        duration: null,
        currentTime: null,
        Goodsdetails: {},
        attention: true,
      };
    },
  
    computed: {
      ...mapState(["all"]),
    },
    methods: {
      //视频详情内容
      goodsdetailEl({ itemId, actorId }) {
        this.axios
          .get("https://apis.netstart.cn/mogujie/video/detail", {
            params: { itemId, actorId },
          })
          .then((res) => {
            this.Goodsdetails = res.data.data.itemExplainList[0];
          });
      },
    },
    created() {
      //视频回放详情
      this.goodsdetailEl({
        itemId: this.$route.query.itemId,
        actorId: this.$route.query.actorId,
      });
    },
  };
  </script>
  
  <style lang="less" scoped>
  .videopage {
    width: 375rem;
    height: 100vh;
    video {
      width: 100%;
      height: 100%;
    }
    .fa-times {
      position: absolute;
      top: 10rem;
      right: 10rem;
      font-size: 18rem;
    }
    .list {
      position: absolute;
      top: 40%;
      right: 10rem;
      display: flex;
      flex-direction: column;
      // justify-content: space-between;
      align-items: center;
      .photo {
        width: 50rem;
        height: 50rem;
        border: 1rem solid rgb(255, 255, 255);
        border-radius: 50%;
        position: relative;
        background-position: center;
        background-size: cover;
        @keyframes imgrond {
          0% {
            transform: scale(1);
          }
          100% {
            transform: scale(0.85);
          }
        }
        @keyframes borderrond {
          0% {
            transform: scale(1);
            opacity: 1;
          }
          100% {
            transform: scale(1.5);
            opacity: 0;
          }
        }
        img {
          width: 50rem;
          border-radius: 50%;
        }
        @keyframes viewicon {
          0% {
            opacity: 0;
          }
          100% {
            opacity: 1;
          }
        }
        
        .attention {
          p {
            background: #ff4466;
            border-radius: 50rem;
            padding: 0.5rem 5rem;
            display: flex;
            align-items: center;
            justify-content: center;
            color: #fff;
            position: absolute;
            bottom: -7rem;
            left: 7rem;
          }
          .attentioned {
            width: 16rem;
            height: 16rem;
            background-color: #ff4466;
            background-size: 90%;
            border-radius: 50%;
            color: #fff;
            position: absolute;
            bottom: -10rem;
            left: 17rem;
            display: flex;
            align-items: center;
            justify-content: center;
            i {
              font-size: 21rem;
            }
          }
        }
      }
      .star {
        width: 40rem;
        height: 40rem;
        background-image: url(../assets/playback-staricon.png);
        background-position: 0rem 0rem;
        background-size: 100%;
        background-repeat: no-repeat;
        margin-top: 20rem;
      }
      .newicon {
        width: 40rem;
        height: 40rem;
        background-image: url(../assets/playback-livenews.png);
        background-position: 0rem 0rem;
        background-size: 100%;
        background-repeat: no-repeat;
        margin-top: 20rem;
      }
      .shoppingcar {
        width: 40rem;
        height: 40rem;
        background-image: url(../assets/playback-shoppingcar.png);
        background-position: 0rem 0rem;
        background-size: 100%;
        background-repeat: no-repeat;
        margin-top: 20rem;
      }
    }
    .infor {
      position: absolute;
      top: 75%;
      left: 20rem;
      color: #fff;
      text-align: left;
      h4 {
        font-size: 16.5rem;
      }
      p {
        color: #d6d6d6;
        margin-top: 5rem;
        span {
          font-size: 13rem;
        }
      }
    }
    .minigoods {
      width: 345rem;
      background: rgba(0, 0, 0, 0.293);
      position: absolute;
      top: 82%;
      left: 15rem;
      display: flex;
      align-items: center;
      justify-content: space-between;
      box-sizing: border-box;
      padding: 6rem;
      border-radius: 5rem;
      color: #fff;
      .img {
        width: 48rem;
        height: 48rem;
        background-size: 100%;
        background-repeat: no-repeat;
      }
  
      .price {
        width: 175rem;
        text-align: left;
  
        h3 {
          width: 175rem;
          font-size: 14rem;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
        }
        .icon {
          display: flex;
          align-items: center;
          p {
            padding: 0rem 3rem;
            background-image: linear-gradient(90deg, #f57f2f, #fc4e3e);
            border-radius: 3rem;
          }
          span {
            font-size: 18rem;
            font-weight: bolder;
            color: #ff3975;
          }
        }
      }
      .buy {
        width: 90rem;
        height: 25rem;
        background: #ff4466;
        border-radius: 100rem;
        text-align: center;
        line-height: 25rem;
      }
    }
    
  }
  </style>
  